.p-px{
  @apply px-[15px]
}
#light .answer {
  @apply xl:mx-[80px] mx-[10px] py-[10px] my-[16px] flex flex-row-reverse text-right items-center bg-[#ffffff] rounded-[14px] cursor-pointer;
  text-transform: uppercase;
  position: relative;
  transition-duration: 0.2s;
  overflow: hidden;
  /* box-shadow: 0 5px 15px #193047; */
}
#dark .answer {
  @apply xl:mx-[80px] mx-[10px] py-[10px] my-[16px] flex flex-row-reverse text-right items-center bg-[#3A4C69] text-white rounded-[14px] cursor-pointer;
  text-transform: uppercase;
  position: relative;
  transition-duration: 0.2s;
  overflow: hidden;
  /* box-shadow: 0 5px 15px #193047; */
}

.answer:after {
  content: "";
  background: #c9c9d0;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px !important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s;
}

.answer:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s;
}

